}

.hidden {
  display: none;
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cbbea5;
  margin: 40px auto;
}

.fadeIn {
  animation: slideIn 0.5s ease-out forwards;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(15%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.move-up {
  opacity: 0;
  -webkit-transform: translateY(15%);
  transform: translateY(15%);
}

.pill {
  width: max-content;
  padding: 4px 16px;
  border-radius: 100px;
  background: transparent;
  box-shadow: 0px 0px 1px 0px #1d1c1d;
  margin: 10px 0;
}
.pill-white {
  box-shadow: 0px 0px 1px 0px #ffffff;
}
.pill-single {
  margin: auto;
}
.pill__label {
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  text-transform: uppercase;
  text-align: center;
}
.pill__label-white {
  color: #ffffff;
}
.pill__label-single {
  border-radius: 100px;
  background: var(--monochrome-black, #1d1c1d);
  box-shadow: 0px 0px 1px 0px #fff;
  margin: auto;
}

.splide-form {
  width: 100%;
  max-width: 480px;
  overflow: hidden;
  background-color: #cbbea5;
  padding-right: 0;
}
.splide-form__arrow--next {
  top: 94% !important;
  left: 7em !important;
}
.splide-form__arrow--prev {
  top: 94% !important;
  left: 10% !important;
}

.splide-testimonials {
  padding: 0 !important;
}
.splide-testimonials__arrow--next {
  top: 97% !important;
  left: 50px !important;
  right: 0 !important;
  margin: auto !important;
}
.splide-testimonials__arrow--prev {
  top: 97% !important;
  left: 0 !important;
  right: 50px !important;
  margin: auto !important;
}

.splide-timeline {
  max-width: 400px;
  margin: auto;
  text-align: center;
}
.splide-timeline__arrow--next {
  background-color: #ffffff !important;
}
.splide-timeline__arrow--prev {
  background-color: #ffffff !important;
}

.splide__arrow svg {
  fill: #1d1c1d !important;
  height: 1.5em !important;
  width: 1.5em !important;
}

.splide__arrow:before {
  content: " ";
  position: absolute;
  top: 19px;
  right: -2px;
  opacity: 1;
  width: 30px;
  height: 30px;
  margin-top: -19px;
  border-radius: 50%;
  border: 1px solid #1d1c1d;
  transition: opacity 0.2s, top 0.2s, right 0.2s;
}

.splide__pagination__page.is-active {
  background-color: #1d1c1d !important;
}

.splide__arrow:focus-visible {
  outline: none !important;
}

.splide__pagination {
  bottom: 38px !important;
}

.team-card {
  border: 1px solid #1d1c1d;
  padding: 32px;
  margin: 32px 0;
  max-width: 300px;
  height: max-content;
  background-color: #f2f1ed;
}
.team-card--slide {
  border: none;
}
.team-card__pill {
  margin: 24px 0 18px;
}
.team-card__image {
  width: 100%;
}
.team-card__name {
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
  letter-spacing: -1.6px;
}
.team-card__title {
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  text-transform: uppercase;
  padding: 8px 0 16px;
}
.team-card__email {
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
}
.team-card__email a {
  color: #1d1c1d;
  text-decoration: underline;
}

.plus {
  position: absolute;
  right: 30px;
  width: 16px;
  height: 12px;
  margin-top: -6px;
  transition: all 250ms;
  top: 26px;
  cursor: pointer;
}
.plus::after {
  position: absolute;
  top: -2px;
  right: 3px;
  display: block;
  width: 8px;
  height: 8px;
  content: "";
  transform: rotate(-45deg);
  border-bottom: 2px solid #1d1c1d;
  border-left: 2px solid #1d1c1d;
}

.plus-active {
  transition: all 250ms;
  transform: rotate(180deg);
}

.arrow {
  top: 25px;
  right: 30px;
  position: absolute;
  width: 16px;
  height: 12px;
  margin-top: -6px;
  transition: all 250ms;
  cursor: pointer;
}
.arrow::after {
  position: absolute;
  top: -2px;
  right: 3px;
  display: block;
  width: 8px;
  height: 8px;
  content: "";
  transform: rotate(-45deg);
  border-bottom: 2px solid #1d1c1d;
  border-left: 2px solid #1d1c1d;
}

.arrow-active {
  transition: all 250ms;
  transform: rotate(180deg);
}

.scroll-text {
  overflow: hidden;
  position: relative;
}

.scroll-text * {
  display: table;
  white-space: pre;
  position: relative;
  /* animation properties */
  -moz-transform: translateX(100%);
  -webkit-transform: translateX(100%);
  transform: translateX(100%);
  -moz-animation: my-animation 35s linear infinite;
  -webkit-animation: my-animation 35s linear infinite;
  animation: my-animation 35s linear infinite;
}

/* for Firefox */
@-moz-keyframes my-animation {
  from {
    -moz-transform: translateX(100%);
  }
  to {
    -moz-transform: translateX(-100%);
  }
}
/* for Chrome */
@-webkit-keyframes my-animation {
  from {
    -webkit-transform: translateX(100%);
  }
  to {
    -webkit-transform: translateX(-100%);
  }
}
@keyframes my-animation {
  0% {
    left: 100%;
    transform: translateX(0);
  }
  100% {
    left: 0;
    transform: translateX(-100%);
  }
}
