}

.timeline-container {
  position: relative;
  width: 100%;
  height: 50px;
  overflow: hidden;
}

.timeline-container:before {
  left: 0;
  content: "";
  position: absolute;
  z-index: 2;
  top: 0;
  height: 100%;
  width: 100px;
}

.timeline-container:after {
  right: 0;
  content: "";
  position: absolute;
  z-index: 2;
  top: 0;
  height: 100%;
  width: 100px;
}

.timeline-container .timeline {
  position: absolute;
  display: block;
  height: 50px;
  transition: left 0.3s ease-in-out;
}

.timeline-container .timeline ol {
  display: block;
  width: 100%;
  height: 2px;
  background-color: #dfe3e6;
  list-style: none;
  padding-left: 210px;
  padding-right: 300px;
}

.timeline-container .timeline ol li {
  display: inline-block;
  padding: 5px;
  margin-top: -11px;
  margin-left: 76px;
  border-radius: 50%;
  border: 3px solid #7f9298;
  background-color: #fff;
  position: relative;
  cursor: pointer;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.timeline-container .timeline ol li.active {
  box-shadow: none;
}

.timeline-container .timeline ol li.active:before {
  content: "";
  display: block;
  height: 25px;
  width: 1px;
  position: absolute;
  top: -25px;
  transition: opacity 0.3s ease-in-out;
}

.timeline-container .timeline ol li.active:after {
  content: "";
  display: block;
  height: 25px;
  width: 1px;
  position: absolute;
  bottom: -25px;
  transition: opacity 0.3s ease-in-out;
}

.timeline-container .btn-back,
.timeline-container .btn-next {
  display: inline-block;
  position: absolute;
  cursor: pointer;
  margin-top: -2px;
  z-index: 11;
  transition: all 0.3s ease;
}

.timeline-container .btn-back.hide,
.timeline-container .btn-next.hide {
  display: none;
}

.timeline-container .btn-back:hover,
.timeline-container .btn-next:hover {
  border-color: #7f9298;
}

.timeline-container .btn-back {
  left: -7px;
  top: -12px;
}

.timeline-container .btn-next {
  right: -7px;
  top: -12px;
}

