.careers {
  font-family: "Inter", sans-serif;
}
.careers__hero__headline {
  color: #f2f1ed;
  text-align: center;
  font-size: 128px;
  font-weight: 700;
  line-height: 100%; /* 128px */
  letter-spacing: -6.4px;
  position: relative;
}
.careers__hero__box {
  width: 150px;
  height: 150px;
  position: absolute;
}
.careers__hero__box--orange {
  top: 140px;
  left: 250px;
}
.careers__hero__box--taupe {
  top: 400px;
  right: 220px;
}
.careers__hero__images {
  width: 150px;
  position: absolute;
}
.careers__hero__images--one {
  top: 400px;
  left: 70px;
}
.careers__hero__images--two {
  top: 300px;
  right: 0px;
}
.careers__hero__images--three {
  top: 130px;
  right: 90px;
}
.careers__hero__images--four {
  top: 130px;
  left: 30px;
}
.careers__headline {
  font-size: 72px;
  font-weight: 700;
  line-height: 100%; /* 72px */
  letter-spacing: -3.6px;
  padding-bottom: 32px;
}
.careers__headline--white {
  color: #ffffff;
}
.careers__copy {
  font-size: 16px;
  font-weight: 400;
  line-height: 150%; /* 24px */
}
.careers__list {
  color: #ffffff;
