.careers__list--black {
  color: #1d1c1d;
}
.careers__list li {
  padding-bottom: 10px;
}

.accordion-careers {
  width: 100%;
  overflow: hidden;
  color: #ffffff;
  border-top: 2px solid #ffffff;
  border-bottom: 2px solid #ffffff;
  background-color: #1d1c1d;
}
.accordion-careers:last-child {
  border-bottom: 2px solid #ffffff;
}
.accordion-careers__title {
  font-size: 0.8rem;
  font-weight: 500;
}
.accordion-careers__wrapper {
  position: relative;
  height: 52px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 15px;
  cursor: pointer;
}
.accordion-careers__wrapper:hover {
  background-color: #f2f1ed;
  color: #1d1c1d;
}
.accordion-careers__wrapper .arrow-down {
  position: absolute;
  top: 15px;
  right: 4px;
  max-width: 15px;
}
.accordion-careers__wrapper .arrow-down::after {
  position: absolute;
  top: -2px;
}
.accordion-careers__intro {
  font-size: 20px;
  font-weight: 700;
  line-height: 140%; /* 28px */
  letter-spacing: -0.6px;
}
@media (max-width: 1023px) {
  .accordion-careers__intro--hide {
    display: none;
  }
}
.accordion-careers__content {
  max-height: 0;
  overflow: hidden;
  will-change: max-height;
  transition: all 0.25s ease-out;
  color: #1d1c1d;
  opacity: 0;
  font-size: 0.9rem;
  line-height: 22px;
  background-color: #f2f1ed;
  padding: 0 15px;
}
.accordion-careers__button {
  background-color: transparent;
  padding: 12px 24px;
  text-transform: capitalize;
  color: #1d1c1d;
  border: 1px solid #1d1c1d;
  margin: 30px 0;
}
.accordion-careers__button a {
  color: #1d1c1d;
}
.accordion-careers__active {
  background-color: #f2f1ed;
  color: #1d1c1d;
}
.accordion-careers__active .accordion-careers__content {
  margin-bottom: 15px;
  opacity: 1;
}
.accordion-careers__active .arrow {
  transition: all 250ms;
  transform: rotate(180deg);
}