html,
body {
  width: 100%;
  height: 100%;
}

body {
  font-family: 'Inter', sans-serif;
  font-size: 1rem;
  display: flex;
  flex-direction: column;
}

* {
  box-sizing: border-box;
}

button {
  display: block;
  font-family: 'Inter', sans-serif;
  text-decoration: none;
  font-size: 0.75rem;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease 0s;
  background-color: $black;
  border: 1px solid $black;
  color: $white;
  padding: 8px 16px;
  text-transform: uppercase;
  line-height: 150%;
}

*:focus {
  outline: none;
}

.header {
  position: fixed;
  width: 100%;
  z-index: 10;
}

.main-container {
  width: 100%;
  flex: 1 0 auto;
  margin-top: 78px;
}

.section-headline {
  font-size: 4.5rem;
  font-style: normal;
  font-weight: 700;
  line-height: 100%;
  letter-spacing: -3.6px;
}

.blue {
  color: $blue;
}

.orange {
  color: $orange;
}

.green {
  color: $green;
}

.black-bg {
  background-color: $black !important;
}

.taupe-bg {
  background-color: $taupe;
}

.blue-bg {
  background-color: $blue;
}

.orange-bg {
  background-color: $orange;
}

.green-bg {
  background-color: $green;
}

.whitesmoke-bg {
  background-color: $whitesmoke;
}

figure {
  margin: 0;
}

img {
  width: 100%;
}

.max-width {
  max-width: 1440px;
  margin: 0 auto;
}

.max-padding {
  padding: 128px;
  @include bp(tablet) {
    padding: 128px 32px;
  }
}

.center-text {
  text-align: center;
}

.left-text {
  text-align: left;
}

.margin-center {
  margin: 0 auto;
}

//Hide broken image placeholder while lazyloading
img[src=''],
img:not([src]) {
  visibility: hidden;
}

.hidden {
  display: none;
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid $taupe;
  margin: 40px auto;
}
